import React from "react";
import styles from './index.module.scss'
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { formApi } from "../../api/form";

const EmailRegEx = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
const UrlRegEx = /^(http|https):\/\/[^ "]+$/;

class Footer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            website: '',
            textarea: '',
            invalidName: false,
            invalidEmail: false,
            invalidWebsite: false,
            showModal: false,
        }
    }
    onChangeInfo = (value, input) => {
        switch (input) {
            case 'name':
                this.setState({ name: value });
                break;
            case 'email':
                this.setState({ email: value });
                break;
            case 'website':
                this.setState({ website: value });
                break;
            case 'textarea':
                this.setState({ textarea: value });
                break;
            default:
                return null;
        }
        this.validate(false);
    };
    validate = (force) => {

        let oneOf = false;
        let invalid = false;

        invalid = this.state.name === '';
        if(!invalid || force ){
            this.setState({ invalidName: invalid });
        }
        oneOf = oneOf || invalid;

        invalid = !EmailRegEx.test(this.state.email);
        if(!invalid || force ){
            this.setState({ invalidEmail: invalid });
        }
        oneOf = oneOf || invalid;

        invalid = !UrlRegEx.test(this.state.website);
        if(!invalid || force ){
            this.setState({ invalidWebsite: invalid });
        }
        oneOf = oneOf || invalid;

        return oneOf;
    }
    onCloseModal = () => {
        this.setState({ 
            showModal: false,
            name: "",
            email: "",
            website: "",
            textarea: "" 
        });
    };
    onSubmit = () => {

        if(this.validate(true)){
            return;
        }

        const params = {
            name: this.state.name,
            email: this.state.email,
            website: this.state.website,
            text: this.state.textarea,
        };
        formApi(params)
            .then(res => this.setState({ 
                showModal: true
            }))
            .catch(err => {
                console.log(err);
            });
    };

    render() {
        return (
            <footer className={styles.footer}>
                <a href={'!#'} name="contacts"> </a>
                <div className={styles.footer_block}>
                    <div className={styles.form_block}>
                        <div className={styles.caption}>Contact us:</div>
                        <div className={this.state.invalidName ? `${styles.input_block} ${styles.name} ${styles.border_red}` : `${styles.input_block} ${styles.name}`}>
                            <input className={styles.input} type="text" placeholder="Your Name" value={this.state.name}
                                onChange={(event) => this.onChangeInfo(event.target.value, 'name')} />
                        </div>
                        <div className={styles.separator}></div>
                        <div className={this.state.invalidEmail ? `${styles.input_block} ${styles.email} ${styles.border_red}` : `${styles.input_block} ${styles.email}`}>
                            <input className={styles.input} type="email" placeholder="E-mail" value={this.state.email}
                                onChange={(event) => this.onChangeInfo(event.target.value, 'email')} />
                        </div>
                        <div className={styles.separator}></div>
                        <div className={this.state.invalidWebsite ? `${styles.input_block} ${styles.company} ${styles.border_red}` : `${styles.input_block} ${styles.company}`}>
                            <input className={styles.input} type="text" placeholder="Link to Your Website or Mobile App" value={this.state.website}
                                onChange={(event) => this.onChangeInfo(event.target.value, 'website')} />
                        </div>
                        <div className={styles.separator}></div>
                        <div className={styles.textarea_block}>
                            <textarea className={styles.textarea_text} placeholder="Your message" value={this.state.textarea}
                                onChange={(event) => this.onChangeInfo(event.target.value, 'textarea')}>
                            </textarea>
                        </div>
                        <button tabindex="" className={styles.button} onClick={this.onSubmit}>Send message</button>
                    </div>
                    <div className={styles.footer_info}>Â© 2020 Crypto Pay. Trademarks belong to their respective owners.
                    All rights reserved
                  </div>
                    <Modal show={this.state.showModal} onHide={this.onCloseModal}>
                        <Modal.Header closeButton>
                            <Modal.Title>Thanks!</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>We will contact you as soon as possible.</Modal.Body>
                        <Modal.Footer>
                            <Button variant="primary" onClick={this.onCloseModal}>
                                Close
                            </Button>
                        </Modal.Footer>
                    </Modal>
                </div>
            </footer>
        )
    }
}
export default Footer;
